<template>
  <div class="poster-in">
    <div >
      <div ref="imgContainer" id="imgContainer" >
        <!--<img src="../assets/image/home_21.jpg" alt="" class="backImg">-->
        <div class="inner-content">
          <div class="imgContainer-title">宇视十周年生日快乐</div>
          <div class="imgContainer-title">十年有你，少年依旧</div>
          <img :src="imgObj.imgSrc" alt="" class="imgContainer-img">
          <div class="miss-word">
            <div>认识宇视 {{ imgObj.days }}天</div>
            <div>宇视十周年 生日快乐</div>
            <div class="miss-name">{{ imgObj.name }}</div>
          </div>
        </div>
      </div>
    </div>
    <!--<div class="result" v-if="imgObj.imgUrl" >-->
      <!--&lt;!&ndash;<img :src="imgObj.imgUrl" alt=""  >&ndash;&gt;-->
      <!--<div  class="get-btn">长按保存</div>-->
    <!--</div>-->
  </div>
</template>

<script>
  import html2canvas from 'html2canvas';
  export default {
    name: "poster",
    data(){
      return{
        imgObj:{
          imgSrc:'',
          days:'',
          name:''
        }
      }
    },
    methods:{
      getResult(obj){
        this.imgObj.imgSrc = obj.imgSrc
        this.imgObj.days = obj.days
        this.imgObj.name = obj.name
        let dom = document.getElementById('imgContainer')
        this.$nextTick(() =>{
          html2canvas(this.$refs.imgContainer, {
            // 转换为图片,
            taintTest: false,
            allowTaint: false,
            scale:window.devicePixelRatio,
            width:dom.offsetWidth,//设置canvas尺寸与所截图尺寸相同，防止白边
            height:dom.offsetHeight,//防止白边
            useCORS: true // 解决资源跨域问题
          }).then(canvas => {
            let dataURL = canvas.toDataURL('image/jpeg',1);
            this.$emit('exportImg',dataURL)
          });
        })
      }
    }
  }
</script>

<style scoped>
  .poster-in{
    position: absolute;
    -webkit-touch-callout:none;
    -webkit-user-select:none;
    -khtml-user-select:none;
    -moz-user-select:none;
    -ms-user-select:none;
    user-select:none;
  }
  .imgContainer-title{
    text-align: center;
    font-size: 0.4rem;
    margin-bottom: 10px;
    color: #fff;
    font-weight: bolder;

  }
  #imgContainer{
    position: fixed;
    /*top: 20px;*/
    /*top: -2000px;*/
    /*bottom: 20px;*/
    top: 0;
    left: 0;
    right: 0;
    /*padding: 80px 0;*/
    height: 100%;
    width: 100%;
    overflow: hidden;
    background: #0d97dd;
    bottom: constant(safe-area-inset-bottom);
    bottom: env(safe-area-inset-bottom);
  }

  .imgContainer-img{
    width: 300px;
    height: 300px;
    display: block;
    margin: 0 auto;
  }
  .miss-word{
    width: 80%;
    margin: 0 auto;
    font-size: 0.3rem;
  }
  .miss-name{
    text-align: right;
    margin-top: 10px;
    font-size: 0.4rem;
  }

  .backImg{
    overflow: hidden;
    transform: rotate(90deg)  translate(calc( 100vh / 2 - 100vw / 2) , calc(100vh / 2 - 100vw / 2));
    width: 100vh;
    height: 100vw;
    position: absolute;
    top: 0;
    left: 0;
  }
  .inner-content{
    position: absolute;
    top: 0;
    left: 0;
    z-index: 999;
    width: 100%;
    padding-top: 20px;
  }

</style>
